body,
html {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif !important;
  color: #545454;
  font-size: 14px;
  line-height: 20px;
}

*:focus {
  outline: none;
}

.hidden {
  display: none !important;
}

h1 {
  font-size: 32px;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  line-height: 45px;
  letter-spacing: -1.5px;
  color: #545454;
}

h2 {
  font-size: 24px;
  font-weight: 700;
  font-family: "Inter", sans-serif;
  line-height: 30px;
  letter-spacing: -0.5px;
  display: flex;
  color: #545454;
}

h3 {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.15px;
  font-family: "Lato", sans-serif;
  margin: 0;
}

h4 {
  font-size: 18px;
  font-family: "Lato", sans-serif;
  line-height: 20px;
  margin: 0;
  letter-spacing: 0.15;
}

.subtitle {
  color: #b5b5b5;
  font-size: 16px;
}

small {
  font-size: 12px;
}

a,
a:link,
a:visited {
  color: #555555;
}

a:hover {
  color: #888888;
}

p {
  color: #636363;
}

